import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

export interface IEvoControlError {
    [error: string]: string;
}

@Component({
    selector: 'evo-control-error',
    template: `<div class="evo-error" *ngFor="let errorMsg of errorsMap; let i = index;">
    <span *ngIf="showError(i)">{{ errorMsg }}</span>
</div>
`,
    styles: [`.evo-error{font-size:14px;color:#f22;font-style:italic;margin-top:10px}`],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class EvoControlErrorComponent {
    @Input() errors: any;
    @Input() errorsMessages: IEvoControlError;
    @Input() showCount = 1;

    private defaultErrorMessages: IEvoControlError = {
        required: 'ÐÐ°Ð¿Ð¾Ð»Ð½Ð¸ÑÐµ Ð¿Ð¾Ð»Ðµ',
        email: 'ÐÐµÐ¿ÑÐ°Ð²Ð¸Ð»ÑÐ½Ð¾ ÑÐºÐ°Ð·Ð°Ð½Ð° Ð¿Ð¾ÑÑÐ°',
        phone: 'ÐÐ²ÐµÐ´Ð¸ÑÐµ ÑÐµÐ»ÐµÑÐ¾Ð½',
    };

    get errorsMap(): string[] {
        const errorMessages = {
            ...this.defaultErrorMessages,
            ...(this.errorsMessages || {})
        };
        const errorKeys = Object.keys(this.errors);
        return errorKeys.map((key) => errorMessages[key]);
    }

    showError(index: number): boolean {
        return ++index <= this.showCount;
    }
}
